import loadjs from 'loadjs';

// inject styles
require('./vivocha.css');

const waitForElementToBeAdded = (cssSelector, cb) => {
  const interval = setInterval(() => {
    const element = document.querySelector(cssSelector);

    if (element) {
      clearInterval(interval);

      if (typeof cb === 'function') {
        cb(element);
      }
    }
  }, 100);
};

window.__va__(function(app) {
  const [cfg] = app.config.files.filter(
    file => file.name === 'vivocha' && file.type === 'script'
  );

  if (!cfg || !cfg.options) {
    console.warn('vivocha config is empty or missed options');
    return;
  }

  // [1] Load vivocha by url from options.
  loadjs(cfg.options.url);

  // [2] Add ready handlers.
  window.vvc_ready_handlers = [];

  // [3] Add vivocha ready function.
  window.vivocha = {
    ready(_cb) {
      window.vvc_ready_handlers.push(_cb);
    },
  };

  const getWidgetId = className => {
    let cn = className;
    cn = JSON.stringify(cn);
    cn = cn.replace(/\"/g, '');
    cn = cn.slice(9);
    return cn;
  };

  // On vivocha ready
  window.vivocha.ready(function() {
    // [4] Prepare selector by which we will find an compaign element.
    const selector = `div[class^="vvc-wrap-${cfg.options.id}"]`;

    // [5] Wait matching DOM element.
    waitForElementToBeAdded(selector, element => {
      if (element) {
        // [6] Get widget id.
        const id = getWidgetId(element.className);

        // [7] Get widget by this classname.
        const widget = window.vivocha.getWidget(id);

        // [8] Engage chat at the command of the user.
        app.on('engageLiveChat', () => {
          if (widget) {
            widget.engage('chat');
          }
        });
      }
    });
  });

  // the iframe may be added by itself
  // if a connection with the agent is established.
  // [9] Wait vivocha iframe and send it to via app.emit().
  waitForElementToBeAdded('#vvc-interaction-app', element => {
    if (element) {
      element.addEventListener('load', () => {
        app.emit('iframe', element);
      });
    }
  });
});
